// Mixins

@mixin font-size($size) {
    font-size: $size;
    font-size: rem($size); }

@mixin opacity($opacity) {
    @if $opacity == 0 {
        visibility: hidden; }
    @else {
        visibility: visible; }
    $opacity-ie: $opacity * 100;
    filter: alpha(opacity=$opacity-ie);
    opacity: $opacity; }

@mixin clear {
    clear: both;
    content: '';
    display: table; }
